html {
  box-sizing: border-box;
  font-size: calc(100vw / 144);

  @include mobile {
    font-size: calc(100vw / 42);
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
  background-color: #fff;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

::-webkit-scrollbar {
  width: 0;
}

.text-row {
  display: block;
}

.btn {
  display: inline-block;
  position: relative;
  z-index: 2;
  padding: 1.8rem 3.2rem;
  color: #FFF;
  font-family: "Montserrat";
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 500;
  line-height: 145%;
  transition: background-color .3s ease-out;

  &--yel {
    background-color: var(--accent-color);

    @include hover {
      background-color: #EFD798;
    }
  }
}

.burger-menu {
  width: 9.7rem;
  height: 3.9rem;

  svg {
    width: 100%;
    height: 100%;
  }
}

.title {
  margin: 0;
  color: #000;
  font-family: "Forum";
  font-size: 3.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: 143.52%;
  text-transform: uppercase;

  @include mobile {
    font-size: 2.4rem;
  }
}

.progress {
  position: fixed;
  z-index: 10;
  right: 0;
  top: 0;
  bottom: 0;
  width: 1.4rem;
  background: rgba(221, 221, 221, 0.36);

  @include mobile {
    display: none;
  }

  &__line {
    background: rgba(211, 188, 128, 0.55);
    width: 100%;
    height: var(--progress-height);

    will-change: height;
  }
}